/* classic title and version */
.app-title {
  margin: 0;
  font-size: 1.375rem;      /* about 22px */
  line-height: 1.25;
  font-weight: 800;
  letter-spacing: .2px;
  color: #0f172a;           /* slate-900 vibe */
}

.version-badge {
  display: inline-block;
  margin-top: 4px;
  padding: 2px 8px;
  font-size: .875rem;
  line-height: 1.4;
  color: #475569;           /* slate-600 */
  background: #f1f5f9;      /* slate-100 */
  border: 1px solid #e2e8f0;/* slate-200 */
  border-radius: 6px;
}

/* Help accordion */
.help-card {
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  background: #ffffff;
  overflow: hidden;
}

.help-summary {
  cursor: pointer;
  padding: 12px 14px;
  font-weight: 700;
  list-style: none;
  background: #f9fafb;
}

.help-card[open] .help-summary {
  border-bottom: 1px solid #e5e7eb;
}

.help-body {
  padding: 14px;
}

.help-section + .help-section {
  margin-top: 16px;
}

.help-section h3 {
  font-size: 1.05rem;
  font-weight: 700;
  margin: 0 0 6px;
}

.help-pre {
  background: #f3f4f6;
  border: 1px solid #e5e7eb;
  border-radius: 6px;
  padding: 10px;
  white-space: pre-wrap;
  font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  font-size: 0.92rem;
  line-height: 1.35;
  margin: 8px 0;
}

.help-actions {
  margin: 6px 0 2px;
}

.btn {
  border: 1px solid #d1d5db;
  background: #ffffff;
  padding: 6px 10px;
  border-radius: 6px;
  cursor: pointer;
}

.btn:hover {
  background: #f9fafb;
}

.help-note {
  color: #6b7280;
  font-size: 0.9rem;
  margin-top: 4px;
}

.help-list {
  list-style: disc;
  padding-left: 1.25rem;
  margin: 0;
}

.help-list li + li {
  margin-top: 4px;
}

/* hard words underline, classic */
.editor-content span[data-hard-word="1"] {
  text-decoration: none;
  border-bottom: 2px dotted #7c3aed; /* purple */
  padding-bottom: 1px;
}


/* sample for legend */
.legend-underline-purple {
  text-decoration: underline;
  text-decoration-style: dotted;
  text-decoration-color: #7c3aed;
  text-underline-offset: 2px;
  font-weight: 700;
}

/* legend squares */
.legend-yellow { background-color: #fff3b0; } /* soft yellow */
.legend-red    { background-color: #ffd1d1; } /* soft red */
.legend-blue   { background-color: #dbeafe; } /* soft blue */

/* sentence highlights by color */
mark[data-highlight-color="yellow"] {
  background-color: #fff3b0;
  border: 1px solid #facc15;
  border-radius: 2px;
  padding: 0 0.08em;
}
mark[data-highlight-color="red"] {
  background-color: #ffd1d1;
  border: 1px solid #f87171;
  border-radius: 2px;
  padding: 0 0.08em;
}
mark[data-highlight-color="blue"] {
  background-color: #dbeafe;
  border: 1px solid #93c5fd;
  border-radius: 2px;
  padding: 0 0.08em;
}


